import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';

@Injectable({
  providedIn: 'root'
})
export class LoginStateService {

  constructor() { }

  private logStateSource= new BehaviorSubject<string>("Logga in");
  private loginRoutSource = new BehaviorSubject<string>('/login');
  currentButtonText = this.logStateSource.asObservable();
  currentRout = this.loginRoutSource .asObservable();

  ChangeLoginButtonText(text: string){
    this.logStateSource.next(text);
  }

  ChangeLoginRout(rout: string){
    this.loginRoutSource.next(rout);
  }
}
