import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CssHelperService {

  constructor() { }

  //Removes boxshadow when screen size < 969
  removeBoxShadow(){
    let shadow;
    if(window.innerWidth > 969){
        shadow = "shadow";
      }else{ 
         shadow = ''
        }
    return shadow;
  }
}
