import { Component, OnInit, HostListener } from '@angular/core';
import { LoginStateService } from 'src/app/login-state.service'
import { CssHelperService } from '../css-helper.service';

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css']
})
export class LogoutComponent implements OnInit {

  loginStateText: string;
  loginRout: string;

  constructor(private loginState: LoginStateService, private cssHelper: CssHelperService) { }

  // Calls the cssHelper service to remove boxshadow for mobiles and tablets on window resize
  @HostListener('window:resize', ['$event']) onReize(event) {
    this.cssHelper.removeBoxShadow();
  }


  ngOnInit() {
    // Calls the cssHelper service to remove boxshadow if intitial screensize matches mobile or tablet
    this.cssHelper.removeBoxShadow();
    
    this.loginState.currentButtonText.subscribe(loginStateText => this.loginStateText = loginStateText);  
    this.loginState.currentRout.subscribe(loginRout => this.loginRout = loginRout);
    this.loginState.ChangeLoginButtonText('Logga in');
    this.loginState.ChangeLoginRout('/login');
  }

  ngAfterContentInit(){
    alert('Du loggas nu ut.');
  }

}
