import { Component, OnInit } from '@angular/core';
import { LoginStateService } from 'src/app/login-state.service'

@Component({
  selector: 'page-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {


  loginStateText: string;
  loginRout: string;

  constructor(private loginState: LoginStateService) { }

  ngOnInit() {
    this.loginState.currentButtonText.subscribe(loginStateText => this.loginStateText = loginStateText);  
    this.loginState.currentRout.subscribe(loginRout => this.loginRout = loginRout); 
  }


}
