import { Component, OnInit, HostListener } from '@angular/core';
import { UserService } from '../user.service';
import { CssHelperService } from '../css-helper.service';
import { HttpService } from '../http.service';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  providers: [HttpService]
})
export class DashboardComponent implements OnInit {

  constructor(private user: UserService, private http: HttpService, private cssHelper: CssHelperService) {}

  // Calls the cssHelper service to remove boxshadow for mobiles and tablets on window resize
  @HostListener('window:resize', ['$event']) onReize(event) {
    this.cssHelper.removeBoxShadow();
  }

  ngOnInit() {
    // Calls the cssHelper service to remove boxshadow if intitial screensize matches mobile or tablet
    this.cssHelper.removeBoxShadow();
  }
  
  getUser(e){
    e.preventDefault();
    alert('Din aktivitet är registrerad');
    // this.http.getUserId();
  }

}

